
"use client";

import { useAuth, useFirebase } from '@/components/Auth/AuthProvider';
import { Request } from '@/app/interfaces';
import { useEffect, useState } from 'react';
import { useRouter } from "next/navigation";
import { collection, query, where, getDocs } from "firebase/firestore";

export default function RequestsPage() {
  const { firestorePromises, user } = useAuth();
  const { app } = useFirebase();
  const router = useRouter();
    if (!user) {
      router.push('/login');
      return;
    }
  const [requests, setRequests] = useState<Request[] | null>(null);

  useEffect(() => {
    const fetchRequests = async () => {
      const { getFirestore } = await firestorePromises;
      const db = getFirestore(app);
      const q = query(collection(db, "requests"), where("reciverId", "==", user.uid));
      const querySnapshot = await getDocs(q);
      let arr = [] as Request[];
      querySnapshot.forEach((doc) => {
        // doc.data() is never undefined for query doc snapshots
        arr.push({
          id: doc.id,
          ...doc.data(),
        } as Request);
      });
      setRequests(arr as Request[]);
    };
    fetchRequests();
  }, [app, firestorePromises]);
  return (
    <main className="container mx-auto py-10 px-4">
      <h1 className="text-3xl font-semibold mb-6"></h1>
    </main>
  );
}